import React, { useMemo } from "react";
import PropTypes from "prop-types";
import { Box, Chip, makeStyles } from "@material-ui/core";

FilterViewer.propTypes = {
  filters: PropTypes.object.isRequired,
  onChange: PropTypes.func,
};

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    flexFlow: "row wrap",
    padding: 0,
    alignItems: "center",
    listStyle: "none",
    margin: theme.spacing(2, 0),
    "& > li": {
      pading: theme.spacing(1),
      margin: 0,
      marginRight: theme.spacing(1),
    },
  },
}));

const FILTER_LIST = [
  {
    id: 1,
    getLabel: () => "Giao hng min ph",
    isActive: (filters) => filters.isFreeShip, //active khi isfreeship=true(khi nhan vao checkbox FreeShip)
    isVisible: () => true, //luon luon show
    isRemovable: false, //luon show nen cho no ko the xoa
    onRemove: () => {},
    onToggle: (filters) => {
      //bam click vao de active or ko active va tra ve data moi
      const newFilters = { ...filters };
      if (newFilters.isFreeShip) {
        //neu dang bat thi xoa di
        delete newFilters.isFreeShip;
      } else {
        //ko thi them vao
        newFilters.isFreeShip = true;
      }
      return newFilters;
    },
  },
  {
    id: 2,
    getLabel: () => "C khuyn mi",
    isActive: () => true,
    isVisible: (filters) => filters.isPromotion, //co promotion =true thi show
    isRemovable: true,
    onRemove: (filters) => {
      const newFilters = { ...filters };
      delete newFilters.isPromotion;
      return newFilters;
    },
    onToggle: () => {},
  },
  {
    id: 3,
    getLabel: (filters) =>
      `T ${filters.salePrice_gte} n ${filters.salePrice_lte}`,
    isActive: () => true,
    isVisible: (filters) =>
      Number.parseInt(filters.salePrice_gte) > 0 &&
      Number.parseInt(filters.salePrice_lte) > 0 &&
      Object.keys(filters).includes("salePrice_gte") && //tao thay 1 mang cac key cua object fillter roi xem mang do co chua key salePrice_gte ko
      Object.keys(filters).includes("salePrice_lte"),
    isRemovable: true,
    onRemove: (filters) => {
      const newFilters = { ...filters };
      delete newFilters.salePrice_gte;
      delete newFilters.salePrice_lte;
      return newFilters;
    },
    onToggle: () => {},
  },
  {
    id: 4,
    getLabel: (filters) => `${filters["category.name"]}`,
    isActive: () => true,
    isVisible: (filters) => filters["category.name"],
    isRemovable: true,
    onRemove: (filters) => {
      const newFilters = { ...filters };
      delete newFilters["category.name"];
      delete newFilters["category.id"];
      return newFilters;
    },
    onToggle: () => {},
  },
];
function FilterViewer({ filters = {}, onChange = null }) {
  const classes = useStyles();
  const visibleFilters = useMemo(() => {
    //chi tinh toan lai khi filters thay doi
    return FILTER_LIST.filter((x) => x.isVisible(filters));
  }, [filters]);

  return (
    <Box component="ul" className={classes.root}>
      {visibleFilters.map((x) => (
        <li key={x.id}>
          <Chip
            label={x.getLabel(filters)}
            color={x.isActive(filters) ? "primary" : "default"}
            clickable={!x.isRemovable} //ko remove thi click dc
            onClick={
              x.isRemovable //neu remove thi tra ve null
                ? null
                : () => {
                    if (!onChange) return;
                    const newFilters = x.onToggle(filters);
                    onChange(newFilters);
                  }
            }
            onDelete={
              x.isRemovable
                ? () => {
                    if (!onChange) return;
                    const newFilters = x.onRemove(filters);
                    onChange(newFilters);
                  }
                : null
            }
          ></Chip>
        </li>
      ))}
    </Box>
  );
}

export default FilterViewer;
